.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.app-layout {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.main-layout {
  display: flex;
  flex: 1; 
}

.sidebar {
  width: 220px;
  background: #2c3e50;
  color: white;
  position: fixed; 
  top: 0;
  left: 0;
  bottom: 0;
  padding: 20px 10px;
}

.content {
  flex: 1;
  padding: 20px;
  margin-left: 220px; 
}

.footer {
  background: #2c3e50;
  color: white;
  text-align: center;
  padding: 15px;
}     